import util from '@/config/util'
var cloudApp = {
	init: function(params) {
		let jpush_relation = util.getStorage('jpush_relation');
		if(jpush_relation && util.isMlala()) {
			setTimeout(function(){
/*
				cloudApp.speechRecognizerRead({
					voice_text: '绝对看得见打开',
					id: 1
				}, 1);
*/
				cloudApp.relation = jpush_relation;
				cloudApp.checkconnect();
				cloudApp.initAjpush();
				cloudApp.checkSmartUpdate();
			}, 1000);
		}
		cloudApp.params = params;
	},

	checkSmartUpdate: function() {
		api.addEventListener({
			name:'smartupdatefinish'
		}, function(ret, err){
			api.rebootApp();
		});
	},

	checkconnect: function() {
		api.addEventListener({
			name:'offline'
		}, function(ret, err){
			api.openFrame({
				name: 'frm_connect',
				url: 'widget://html/connect.html',
				bounces: true,
				rect: {
					x: 0,
					y: 0,
					w: 'auto',
					h: 'auto'
				}
			});
			return false;
		});

		if(api.connectType == 'none') {
			api.openFrame({
				name: 'frm_connect',
				url: 'widget://html/connect.html',
				bounces: true,
				rect: {
					x: 0,
					y: 0,
					w: 'auto',
					h: 'auto'
				}
			});
			return false;
		}

		api.addEventListener({
			name:'online'
		}, function(ret, err){
			api.closeFrame({
				name: 'frm_connect'
			});
			var url_cn = cloudApp.igetPrefs('url_cn');
			if(url_cn) {
				window.location.href = url_cn;
			}
		});
	},

	isHasModule: function(name) {
		var status = api.require(name);
		if(!status || status == 'undefined') {
			return false;
		}
		return true;
	},

	scanQRCode: function(params) {
		var FNScanner = api.require('FNScanner');
		FNScanner.open({
			autorotation: true
		}, function(ret, err) {
			if (ret) {
				if(ret.eventType == 'success') {
					if(typeof params.success == 'function') {
						params.success({
							content: ret.content
						});
					} else {
						if(ret.content.indexOf('https://') != -1 || ret.content.indexOf('http://') != -1) {
							window.location.href = ret.content;
						}
					}
				}
			} else {
				if(typeof params.fail == 'function') {
					params.fail({
						content: ret.content
					});
				} else {
					alert(JSON.stringify(ret))
				}
			}
		});
	},

	netAudioPlay: function(extra, times) {
		var netAudio = api.require('netAudio');
		if(times > 0) {
			netAudio.play({
				path: extra.resource
			}, function(ret) {
				if(ret.complete) {
					util.request({
						url: 'system/common/order/check',
						data: {
							id: extra.id,
							order_from: extra.order_from,
						},
					}).then((res) => {
						let result = res.data.message;
						if(!result.errno) {
							times--;
							cloudApp.netAudioPlay(extra, times);
						}
					});
				}
			});
		}
		return true;
	},

	speechRecognizerRead: function(extra, times) {
		var iflyRecognition = api.require('iflyRecognition');
		var android_appid = '5bd167ea';
		var ios_appid = '5bd17b48';
		var xunfei_android_appid = api.loadSecureValue({
			sync: true,
			key: 'xunfei_android_appid'
		});
		if(xunfei_android_appid) {
			android_appid = xunfei_android_appid;
		}
		var xunfei_ios_appid = api.loadSecureValue({
			sync: true,
			key: 'xunfei_ios_appid'
		});
		if(xunfei_ios_appid) {
			ios_appid = xunfei_ios_appid;
		}
		iflyRecognition.createUtility({
			android_appid: android_appid,
			ios_appid: ios_appid
		}, function(ret, err) {
			if(ret.status) {
			} else {
				api.alert({ msg: "创建讯飞失败" });
			}
		});
		if(times > 0) {
			iflyRecognition.read({
				readStr: extra.voice_text,
				speed: 60,
				volume: 100,
				voice: 'xiaoyan',
				rate: 16000
			}, function(ret, err) {
				if(ret.status) {
					if(ret.speakProgress == 100) {
						util.request({
							url: 'system/common/takeout/check',
							data: {
								id: extra.id || extra.order_id
							},
						}).then((res) => {
							let result = res.data.message;
							if(!result.errno) {
								times--;
								cloudApp.speechRecognizerRead(extra, times);
							}
						});
					}
				}
			});
		}
		return true;
	},

	play: function(extra) {
		var prefs = cloudApp.igetPrefs('phonic');
		if(!prefs) {
			prefs = {
				voice: 1,
				vibrate: 1,
				voice_type: 1,
				new: {
					times: 'loop',
				},
				refund: {
					times: 'loop',
				},
				remind: {
					times: 'loop',
				}
			};
		}
		if(prefs.vibrate) {
			api.notification({
				vibrate: [3000, 3000]
			});
		}
		let notify_type = extra.notify_type;
		if(prefs.voice) {
			var times = prefs[notify_type]['times'];
			if(times == 'loop') {
				times = 100;
			} else {
				times = 3;
			}
			var voice_type = prefs['voice_type'];
			if(voice_type == 1) {
				cloudApp.speechRecognizerRead(extra, times);
			} else {
				cloudApp.netAudioPlay(extra, times);
			}
		}
		return true;
	},

	initBle: function() {
		var ble = api.require('ble');
		ble.initManager(function(ret) {
			//alert(ret.state);
			if (ret.state == "poweredOn") {
				//api.alert({ msg: "初始化成功" });
			}
		});
		cloudApp.ble = ble;
	},

	aliPayPlus2: function() {
		var aliPayPlus = api.require('aliPayPlus');
		aliPayPlus.config({
			appId: '2017080608063032',
			rsaPriKey: '',
		}, function(ret, err) {
			if(!ret.status) {
				alert('支付宝配置错误码:' + err.code);
				return false;
			}
			aliPayPlus.pay({
				subject: '支付宝支付测试商品',
				body: '交易商品的简介',
				amount: '0.01',
				tradeNO: '20190311100112345678913',
				rsa2: true
			}, function(ret, err) {
				if(ret.code != 9000) {
					alert('支付宝支付结果错误码:' + ret.code);
					return false;
				}
				api.alert({
					title: '支付结果',
					msg: JSON.stringify(ret),
					buttons: ['确定']
				});
			});
		});
	},

	wxPay: function() {

	},

	wxPay2: function() {
		var wxPay = api.require('wxPay');
		wxPay.config({
			apiKey: '',
			mchId: '1515090771',
			partnerKey: 'rVadyDZj07jJLjj90DJeg7X8Abj0x9jR',
			notifyUrl: 'https://www.baidu.com'
		}, function(ret, err) {
			if (ret.status) {
				alert('配置商户支付参数成功');
				wxPay.pay({
					description: 'iPad mini 16G 白色',
					totalFee: '1',
					tradeNo: '1217752501201407033233368020',
					spbillCreateIP: '196.168.1.1',
					deviceInfo: '013467007045764',
					detail: 'iPad mini 16G 白色',
					attach: '说明',
					feeType: 'CNY',
					timeStart: '20190308091010',
					timeExpire: '20190408091010',
					goodsTag: 'WXG',
					productId: '12235413214070356458058',
					openId: util.getStorage('openId')
				}, function(ret, err) {
					if (ret.status) {
						alert('wxPayPay返回值:' + JSON.stringify(ret));
					} else {
						alert('wxPayPay错误号:' + err.code);
					}
				});
			} else {
				alert('wxPayConfig错误号:' + err.code);
			}
		});
	},

	wxLogin: function() {
		var wx = api.require('wx');
		wx.isInstalled(function(ret, err) {
			if(!ret.installed) {
				alert('当前设备未安装微信客户端');
				return;
			}
			//获取code
			wx.auth({
				apiKey: ''
			}, function(ret, err) {
				var code = ''
				if (ret.status) {
					code = ret.code;
					util.setStorage('code', code);
					alert(JSON.stringify('code:' + code));
					if(code) {
						//获取accessToken
						wx.getToken({
							apiKey: '',
							apiSecret: '',
							code: code
						}, function(ret, err) {
							if (ret.status) {
								util.setStorage('accessToken', ret.accessToken, ret.expires)
								util.setStorage('dynamicToken', ret.dynamicToken)
								util.setStorage('openId', ret.openId)
								var accessToken = ret.accessToken;
								var dynamicToken = ret.dynamicToken;
								var openId = ret.openId;
								//获取用户信息getUserInfo
								wx.getUserInfo({
									accessToken: accessToken,
									openId: openId
								}, function(ret, err) {
									if (ret.status) {
										util.setStorage('userInfo', ret);
										alert('userInfo:' + JSON.stringify(ret));
									} else {
										alert('getUserInfo错误码:' + err.code);
										var errorCode = err.code;
										if(errorCode == 1) {
											//刷新accessToken
											wx.refreshToken({
												apiKey: '',
												dynamicToken: dynamicToken
											}, function(ret, err) {
												if (ret.status) {
													alert('refreshToken返回值:' + JSON.stringify(ret));
													util.setStorage('accessToken', ret.accessToken, ret.expires)
													util.setStorage('dynamicToken', ret.dynamicToken)
													util.setStorage('openId', ret.openId)
													//重新调用getUserInfo
													wx.getUserInfo({
														accessToken: ret.accessToken,
														openId: ret.openId
													}, function(ret, err) {
														if (ret.status) {
															util.setStorage('userInfo', ret);
															alert('userInfo:' + JSON.stringify(ret));
														} else {
															alert('getUserInfo错误码:' + err.code);
														}
													});
												} else {
													alert('refreshToken错误码:' + err.code)
												}
											});
										}
									}
								});
							} else {
								alert('getToken错误码:' + err.code);
							}
						});
					}
				} else {
					alert('auth错误码:' + err.code);
				}
			});
		});
	},

	initAjpush: function(){
		var ajpush = api.require('ajpush');
		ajpush.setBadge({
			badge:0
		});
		ajpush.init(function(ret) {
			if(ret && ret.status){}
		});
		//同步推送标签和别名
		var push_code = cloudApp.igetPrefs('push_code');
		if(cloudApp.relation && cloudApp.relation.code && (!push_code || (push_code && push_code != cloudApp.relation.code))) {
			var bindParams = {};
			if(cloudApp.relation.alias) {
				bindParams['alias'] = cloudApp.relation.alias;
			}
			if(cloudApp.relation.tags) {
				bindParams['tags'] = cloudApp.relation.tags;
			}
			ajpush.bindAliasAndTags(bindParams, function(ret, err) {
				var statusCode = ret.statusCode;
				if(statusCode == 0) {
					cloudApp.isetPrefs('push_code', cloudApp.relation.code);
				} else {
					alert("同步推送信息出错：" + statusCode);
				}
			});
		}

		ajpush.setListener(function(ret) {
			var extra = ret.extra;
			try {
				extra = JSON.parse(ret.extra);
			} catch(e) {}
			if(api.systemType == 'ios') {
				var ajpush = api.require('ajpush');
				ajpush.setBadge({
					badge:0
				});
			}
			var clerkInfo = util.getStorage('clerkInfo');
			if(clerkInfo && clerkInfo.token) {
				cloudApp.play(extra);
				if(cloudApp.params && (cloudApp.params.page == 'orderTakeout' || cloudApp.params.page == 'orderPaotui')) {
					cloudApp.params.vue.onLoad(true);
				}
			}
		});

		api.addEventListener({
			name: 'appintent'
		}, function(ret, err) {
			if(ret && ret.appParam.ajpush) {
				var ajpush = ret.appParam.ajpush;
				try {
					var extra = JSON.parse(ajpush.extra);
				} catch(e) {
					var extra = ajpush.extra;
				}
				location.href = extra.url;
			}
		});

		api.addEventListener({
			name: 'noticeclicked'
		}, function(ret, err) {
			if(ret && ret.value) {
				var ajpush = api.require('ajpush');
				ajpush.setBadge({
					badge:0
				});
				var ajpush = ret.value;
				var extra = ajpush.extra;
				location.href = extra.url;
			}
		});
	},

	igetPrefs: function(key) {
		var keys = key.split('.');
		var data = '';
		if(keys.length == 1) {
			data = api.getPrefs({
				sync: true,
				key: key
			});
			try {
				data = JSON.parse(data);
			} catch(e) {}
		} else if(keys.length == 2) {
			var all = cloudApp.igetPrefs(keys[0]);
			if(all && all[keys[1]]) {
				data = all[keys[1]];
			}
		} else if(keys.length == 3) {
			var all = cloudApp.igetPrefs(keys[0]);
			if(all && all[keys[1]]) {
				data = all[keys[1]][keys[2]];
			}
		}
		return data;
	},

	isetPrefs: function(key, params) {
		var keys = key.split('.');
		if(keys.length == 1) {
			api.setPrefs({
				key: key,
				value: params
			});
		} else if(keys.length == 2) {
			var all = cloudApp.igetPrefs(keys[0]);
			if(!all) {
				all = {};
			}
			all[keys[1]] = params;
			api.setPrefs({
				key: keys[0],
				value: all
			});
		} else if(keys.length == 3) {
			var all = cloudApp.igetPrefs(keys[0]);
			if(!all) {
				all = {};
			}
			if(!all[keys[1]]) {
				all[keys[1]] = {};
			}
			all[keys[1]][keys[2]] = params;
			api.setPrefs({
				key: keys[0],
				value: all
			});
		}
		return true;
	},

	gMapNavigation: function(params) {
		var navigator = api.require('navigator');
		if(!navigator) {
			alert('没有安装navigator模块');
			return false;
		}
		navigator.installed({
			target: 'gMap'
		}, function(ret, err) {
			if(!ret.status) {
				if(typeof params.fail == 'function') {
					params.fail();
				}
				return false;
			}
			navigator.gMapNavigation({
				start: {},
				end: {
					lon:  params.lng,
					lat: params.lat
				},
				mode: 'driving'
			});
		});
	}
};
export default cloudApp


